/* мобильные табы */

document.addEventListener('DOMContentLoaded', function () {
  const tabsContainer = document.querySelector('.services-tabs');
  if (!tabsContainer) return;

  const tabButtons = tabsContainer.querySelectorAll('.services-tab');
  const tabContents = document.querySelectorAll('.tab-content');
  if (tabButtons.length === 0 || tabContents.length === 0 || tabButtons.length !== tabContents.length) return;

  let activeTabElement = tabsContainer.querySelector('.tab--active') || tabButtons[0];
  let activeContentElement = document.querySelector('.tab-content.active');
  const initialActiveIndex = Array.from(tabButtons).indexOf(activeTabElement);
  if (!activeContentElement && initialActiveIndex >= 0 && initialActiveIndex < tabContents.length) {
    activeContentElement = tabContents[initialActiveIndex];
    activeContentElement.classList.add('active');
  }
  if (!activeTabElement.classList.contains('tab--active')) {
    activeTabElement.classList.add('tab--active');
  }

  function isMobileBreakpoint() {
    return window.matchMedia('(max-width: 768px)').matches;
  }

  function hideInactiveTabs() {
    tabButtons.forEach(tab => {
      if (tab === activeTabElement) {
        tab.style.display = '';
      } else {
        tab.style.display = 'none';
      }
    });
    tabsContainer.classList.remove('expanded');
  }

  function showAllTabs() {
    tabButtons.forEach(tab => {
      tab.style.display = '';
    });
    tabsContainer.classList.add('expanded');
  }

  function switchTab(newTabElement) {
    const newTabIndex = Array.from(tabButtons).indexOf(newTabElement);
    if (newTabIndex === -1) return;

    if (activeTabElement) activeTabElement.classList.remove('tab--active');
    if (activeContentElement) activeContentElement.classList.remove('active');

    activeTabElement = newTabElement;
    activeTabElement.classList.add('tab--active');

    activeContentElement = tabContents[newTabIndex];
    activeContentElement.classList.add('active');

    if (isMobileBreakpoint()) {
      hideInactiveTabs();
    }
  }

  tabsContainer.addEventListener('click', function (event) {
    const clickedTab = event.target.closest('.services-tab');
    if (!clickedTab) return;

    event.preventDefault();

    if (isMobileBreakpoint()) {
      const isAnyOtherTabVisible = Array.from(tabButtons).some(tab => tab !== activeTabElement && tab.style.display !== 'none');

      if (isAnyOtherTabVisible) {
        switchTab(clickedTab);
      } else {
        if (clickedTab === activeTabElement) {
          showAllTabs();
        } else {
          switchTab(clickedTab);
        }
      }
    } else {
      switchTab(clickedTab);
    }
  });

  window.addEventListener('resize', function () {
    if (isMobileBreakpoint()) {
      hideInactiveTabs();
    } else {
      showAllTabs();
    }
  });

  if (isMobileBreakpoint()) {
    hideInactiveTabs();
  }
});

// Звёздочки
const ratingElement = document.querySelector('.rating--form');

if (ratingElement) {
  const stars = ratingElement.querySelectorAll('span');

  if (stars.length > 0) {
    stars.forEach((star, index) => {
      star.addEventListener('click', function () {
        stars.forEach(s => s.classList.remove('active'));
        for (let i = 0; i <= index; i++) {
          stars[i].classList.add('active');
        }
      });
    });
  }
}

